import { useState, useEffect, useCallback } from 'react';
import { useUser } from '../context/UserContext';

export const useTextToSpeech = () => {
  /* eslint-disable no-undef */
  const [voices, setVoices] = useState<SpeechSynthesisVoice[]>([]);
  /* eslint-enable no-undef */
  const [isSpeaking, setIsSpeaking] = useState(false);
  const [supported, setSupported] = useState(false);

  // Access UserContext (assumes provider exists)
  const { user } = useUser();
  const userVoiceURI = user?.voiceURI;

  useEffect(() => {
    if (typeof window !== `undefined` && `speechSynthesis` in window) {
      setSupported(true);

      const updateVoices = () => {
        setVoices(window.speechSynthesis.getVoices());
      };

      updateVoices();
      window.speechSynthesis.onvoiceschanged = updateVoices;
    }
  }, []);

  const speak = useCallback(
    (text: string, rate = 0.9) => {
      if (!supported) return;

      window.speechSynthesis.cancel(); // Stop any current speech

      const utterance = new SpeechSynthesisUtterance(text);
      utterance.rate = rate; // Slightly slower is better for spelling
      utterance.pitch = 1;

      // Select voice: User preference > Native English > First English
      let selectedVoice;

      if (userVoiceURI) {
        selectedVoice = voices.find((v) => v.voiceURI === userVoiceURI);
      }

      if (!selectedVoice) {
        selectedVoice =
          voices.find((v) => v.lang.startsWith(`en`) && v.localService) ||
          voices.find((v) => v.lang.startsWith(`en`));
      }

      if (selectedVoice) {
        utterance.voice = selectedVoice;
      }

      utterance.onstart = () => setIsSpeaking(true);
      utterance.onend = () => setIsSpeaking(false);
      utterance.onerror = () => setIsSpeaking(false);

      window.speechSynthesis.speak(utterance);
    },
    [supported, voices, userVoiceURI],
  );

  const cancel = useCallback(() => {
    if (supported) {
      window.speechSynthesis.cancel();
      setIsSpeaking(false);
    }
  }, [supported]);

  return { speak, cancel, isSpeaking, supported, voices };
};
