import React, { useEffect, useState } from 'react';
import Layout from '../components/Layout/Layout';
import Button from '../components/Button/Button';
import Card from '../components/Card/Card';
import { useUser } from '../context/UserContext';
import { useTextToSpeech } from '../hooks/useTextToSpeech';

const SettingsPage = () => {
  const { user, updateProgress } = useUser();
  const { voices, speak } = useTextToSpeech();
  /* eslint-disable no-undef */
  const [availableVoices, setAvailableVoices] = useState<
    SpeechSynthesisVoice[]
  >([]);
  /* eslint-enable no-undef */

  useEffect(() => {
    // Filter for English voices roughly
    if (voices.length > 0) {
      setAvailableVoices(voices.filter((v) => v.lang.startsWith(`en`)));
    }
  }, [voices]);

  const handleVoiceChange = (e: React.ChangeEvent<HTMLSelectElement>) => {
    updateProgress({ voiceURI: e.target.value });
  };

  const currentVoice = availableVoices.find(
    (v) => v.voiceURI === user.voiceURI,
  );

  return (
    <Layout>
      <div style={{ maxWidth: `800px`, margin: `0 auto`, padding: `2rem` }}>
        <h1 style={{ marginBottom: `2rem`, color: `var(--color-primary)` }}>
          Settings
        </h1>

        <Card title="Voice Settings">
          <div style={{ padding: `1rem 0` }}>
            <p style={{ marginBottom: `1rem` }}>
              Choose the voice you want to hear for spelling words and
              questions.
            </p>

            <div
              style={{
                display: `flex`,
                gap: `1rem`,
                alignItems: `center`,
                flexWrap: `wrap`,
              }}
            >
              <select
                value={user.voiceURI || ``}
                onChange={handleVoiceChange}
                style={{
                  padding: `0.75rem`,
                  borderRadius: `8px`,
                  border: `1px solid var(--color-gray-300)`,
                  fontSize: `1rem`,
                  minWidth: `300px`,
                  maxWidth: `100%`,
                }}
              >
                <option value="">Default (System Choice)</option>
                {availableVoices.map((voice) => (
                  <option key={voice.voiceURI} value={voice.voiceURI}>
                    {voice.name} ({voice.lang})
                  </option>
                ))}
              </select>

              <Button
                variant="secondary"
                size="small"
                onClick={() =>
                  speak(`Hello! This is how I sound. Spelling Bee is fun!`, 0.9)
                }
              >
                üîä Test Voice
              </Button>
            </div>

            {currentVoice && (
              <p
                style={{
                  marginTop: `1rem`,
                  fontSize: `0.9rem`,
                  color: `var(--color-gray-600)`,
                }}
              >
                Selected: <strong>{currentVoice.name}</strong>
              </p>
            )}
          </div>
        </Card>

        <div style={{ marginTop: `2rem`, textAlign: `center` }}>
          <p style={{ marginBottom: `1rem` }}>More settings coming soon!</p>
          <Button variant="outline" onClick={() => window.history.back()}>
            Back
          </Button>
        </div>
      </div>
    </Layout>
  );
};

export default SettingsPage;
